body {
  margin: 0;
  font-family: 'Inter',-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  max-width: 375;
  background-color: #F5F5F5;
  font-size: 12px;
  font-weight: 500;
  line-height: 15px;
  padding: 0;
  margin: 0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header {
  width: 343px;
  margin: 25px 16px ;
  display: flex;
  justify-content: space-between;
}

.heder_right {
  display: flex;
}

.heder_left {
  height: 34.69px;
  width: 84px;
  background: url("/alex/static/media/Group 270988500.c37c9b9e.svg") 50% 50% / contain no-repeat;
}

.refresh>a,
.user>a {
  display: block;
  width: 16.41px;
  height: 14.68px;
}

.refresh>a {
  background: url("/alex/static/media/Group 270988514.35300b66.svg") 50% 50% / contain no-repeat;
}

.user>a {
  background: url(/alex/static/media/Vector.d3bea51a.svg) 50% 50% / contain no-repeat;

}

.refresh,
.user {
  height: 36px;
  width: 36px;
  background: linear-gradient(#FF9704,#FFC702);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.refresh {
  margin-right: 10px;
}
.balance_section {
  width: 343px;
  height: 190px;
  background-color: #ffffff;
  margin: 20px 16px 0;
  border-radius: 20px;
}

.balance,
.balance_btn {
  margin-left: 14px;
}

.balance_text {
  display: inline-block;
  margin: 18px 0 0;
}

.balance_num {
  margin: 10px 0 0;
}

.balance_btn {
  display: flex;
  margin: 23px 0 0 14px;
}

.balance_btn>button {
  color: #373737;
  background-color: #FFF1E0;
  border-radius: 16px;
  border: none;
  width: 78px;
  height: 70px;
  font-size: 10px;
  line-height: 12px;
  margin: 0 11px 0 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.balance_btn>button>:first-child {
  margin: 16px 0 0 0;
  display: flex;
  align-items: center;
  justify-content: center;
}

.balance_btn>button>:last-child {
  margin: 6px;
}
 
.balance_btn_imgFirst {
  background: url("/alex/static/media/Vector (1).a31c620d.svg") 50% 50% / contain no-repeat;
}

.balance_btn_imgSecond {
  background: url("/alex/static/media/Vector (2).393639c1.svg") 50% 50% / contain no-repeat;
}

.balance_btn_imgThird {
  background: url("/alex/static/media/Vector (3).69d76375.svg") 50% 50% / contain no-repeat;
}

.balance_btn_imgFirst,
.balance_btn_imgSecond,
.balance_btn_imgThird {
  width: 16px;
  height: 16px;
}

.balance_text {
  font-weight: 500;
  font-size: 12px;
  line-height: 14.52px;
  color: #A0A0A0;
}

.balance_num {
  font-weight: 600;
  font-size: 28px;
  line-height: 33.89px;
  color: #373737;
}
main {
  max-width: 375px;

}
main>* {
  max-width: 343px;
}

.payBtn>button {
  width: 109px;
  height: 90px;
  margin-right: 8px;
  border: none;
}

.payBtn {
  display: flex;
  margin: 7px 0 0 0;
}

.pay p {
  margin: 28px 0 0;
  font-size: 15px;
}

.tel,
.ithernet,
.tv {
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 16px;
  background-color: #FFFFFF;
}

.tel p:first-child,
.ithernet p:first-child,
.tv p:first-child,
.tel p:last-child,
.ithernet p:last-child,
.tv p:last-child {
  margin: 0;
}

.tel p:last-child,
.ithernet p:last-child,
.tv p:last-child {
  font-size: 12px;
  color: #373737;
}

.tel p:first-child,
.ithernet p:first-child,
.tv p:first-child {
  margin: 26px 0 14px;
  width: 19px;
  height: 19px;
}

.tel p:first-child {
  margin: 25px 0 12px;
  width: 22px;
  height: 22px;
  background: url(/alex/static/media/ic_prd_mobileapp_default_20_w.09d3ef59.svg) 50% 50% / contain no-repeat;
}

.ithernet p:first-child {
  background: url("/alex/static/media/Union (1).f655a845.svg") 50% 50% / contain no-repeat;
}

.tv p:first-child {
  background: url("/alex/static/media/Union (2).174c1b26.svg") 50% 50% / contain no-repeat;
}

.main_nav {
  background-color: #FFFFFF;
  width: 284px;
  height: 60px;
  border-radius: 20px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin: 10px 0 0 40px;
}

.home,
.replenishment,
.translations,
.bill,
.profile {
  display: flex;
  width: 20px;
  height: 20px;
}

.home {
  background: url(/alex/static/media/Home.aebb7d3c.svg) 50% 50% / contain no-repeat;
}

.replenishment {
  background: url(/alex/static/media/Plus.021ea0ba.svg) 50% 50% / contain no-repeat;
}

.translations {
  background: url("/alex/static/media/Plus (1).942f23cc.svg") 50% 50% / contain no-repeat;
}

.bill {
  background: url("/alex/static/media/Plus (2).fd12cdce.svg") 50% 50% / contain no-repeat;
}

.profile {
  background: url(/alex/static/media/Wallet.fa306def.svg) 50% 50% / contain no-repeat;
}

.last_operation>button {
  margin: 0;
  border: none;
  color: #FF9F04;
  background: none;
  cursor: pointer;
}

.comingBalance_left p {
  margin: 0;
}

.comingBalance_left .comingBalance_left__img {
  display: inline-block;
  background: url(/alex/static/media/Union.aa963a3e.svg) 50% 50% / 13px 14px no-repeat;
  width: 36px;
  height: 36px;
  margin: 17px 10px;
  border-radius: 50%;
  background-color: #F0F0F0;
}

.comingBalance .comingBalance_right {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 15px;
}

.last_operation {
  display: flex;
  justify-content: space-between;
}

main >* {
  margin-left: 16px;
}

.comingBalance_left {
  display: flex;
  justify-content: center;
  align-items: center;
}



.comingBalance {
  display: flex;
  justify-content: space-between;
  background-color: #FFFFFF;
  border-radius: 20px;
}

#plus {
  color: #2C8B47;
}
